import React, { useEffect, useState } from 'react';
import { CiMenuBurger } from 'react-icons/ci';
import './header.css';
import menu1 from '../../img/menu/menu1.jpg'
import menu2 from '../../img/menu/menu2.jpg'
import menu3 from '../../img/menu/menu3.jpg'
import menu4 from '../../img/menu/menu4.jpg'
import { gsap } from 'gsap';

function Headers() {
  const [isNavActive, setIsNavActive] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const tl = gsap.timeline({ paused: true });

  const handleBurgerClick = () => {
    setIsMenuOpen(!isMenuOpen);
    tl.reversed(!tl.reversed());
  };

  
  const handleMenuItemLinkClick = (link) => {
    setIsMenuOpen(false);
    tl.reversed(!tl.reversed());

    // Smooth scroll to the target section
    const targetElement = document.querySelector(link);
    if (targetElement) {
      window.scrollTo({
        top: targetElement.offsetTop,
        behavior: 'smooth',
      });
    }
  };

  const handleMenuCloseClick = () => {
    setIsMenuOpen(false);
    tl.reversed(!tl.reversed());
   
  };

  




  useEffect(() => {
    // Animation logic using gsap
    

    tl.to(".menu-container", 1, {
      left: isMenuOpen ? 0 : '-100%',
      ease: "expo.inOut"
    });

 


    tl.reverse();

    // Event listeners
    document.querySelector('.burger').addEventListener('click', handleBurgerClick);

    document.querySelectorAll('.menu__item-link').forEach((link) =>
    link.addEventListener('click', () => handleMenuItemLinkClick(link.getAttribute('href')))
  );
    // Cleanup the event listeners when the component is unmounted
    // return () => {
    //   document.querySelector('.burger').removeEventListener('click', handleBurgerClick);
      
    //   document.querySelectorAll('.menu__item-link').forEach(link => link.removeEventListener('click', handleMenuItemLinkClick));
    // };
  }, [isMenuOpen]); // Add isMenuOpen to the dependency array

  useEffect(() => {
    const handleScroll = () => {
      const scrollDock = window.scrollY;

      if (scrollDock > 1) {
        setIsNavActive(true);
      } else {
        setIsNavActive(false);
      }
    };
    window.addEventListener('scroll', handleScroll);

    
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <>
      <div className={`nav ${isNavActive ? 'active' : ''}`}>
        <span className="logo anim">mds</span>
        <button className="burger anim" onClick={handleBurgerClick}>
          <span className="burger__line"></span>
          <span className="burger__line"></span>
        </button>
      </div>
      <div className="menu-container">
      
      <div className="socials">
          <a href="#">instagram</a>
          <a href="#">telegram</a>
        </div>
      <div id="menu" class="menu">
      
        <MenuItem link="#home" imgSrc={menu1} alt="menu1" text="home - home - home - home - home - home - home" onClick={() => handleMenuItemLinkClick} />
        <MenuItem link="#popular" imgSrc={menu2} alt="menu2" text="showcase - showcase - showcase - showcase - showcase - showcase - showcase" onClick={() => handleMenuItemLinkClick} />
        <MenuItem link="#about" imgSrc={menu3} alt="menu3" text="about - about - about - about - about - about - about" onClick={() => handleMenuItemLinkClick} />
        <MenuItem link="#contact" imgSrc={menu4} alt="menu4" text="contact - contact - contact - contact - contact - contact - contact" onClick={() => handleMenuItemLinkClick} />
      </div>
        
      </div>
    </>
  );
}

const MenuItem = ({ link, imgSrc, alt, text, onClick }) => {
  return (
    <div className="menu__item">
      <a href={link} className="menu__item-link" onClick={() => onClick}>{link.replace('#', '')}</a>
      <img src={imgSrc} alt={alt} className="menu__item-img" />
      <div className="marquee">
        <div className="marquee__inner">
          <span>{text}</span>
        </div>
      </div>
    </div>
  );
};

export default Headers;
