// Preloader.jsx
import React, { useEffect, useState } from 'react';
import './Preloader.css'; // Make sure to import your CSS file

function Preloader() {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      setLoading(false);
    }, 800);

    return () => clearTimeout(timer);
  }, []);

  return (
    <div className={`preloader ${loading ? '' : 'active'}`}>
      <div className="preloader__box">
        <p className="secondary-text-logo">Design Studio</p>
        <span>Moonkiri</span>
      </div>
    </div>
  );
}

export default Preloader;
