    const topItemElements = topItemRef.current.querySelectorAll('.top__item');

    const handleTopItemClick = (event) => {
      const clickedItem = event.currentTarget;
      const timeAnim = 250;
    
      // Check if clickedItem exists and has classList property
      if (clickedItem && clickedItem.classList) {
        topItemElements.forEach(item => item.classList.remove('active'));
        clickedItem.classList.add('active');
    
        topItemElements.forEach(item => {
          const content = item.querySelector('.top__head')?.nextElementSibling; // Use optional chaining to avoid errors
          if (content) {
            content.style.display = 'none';
          }
        });
    
        const contentToShow = clickedItem.querySelector('.top__head')?.nextElementSibling;
        if (contentToShow) {
          contentToShow.style.display = 'block';
        }
    
        const id = clickedItem.dataset.id;
        const topImgItems = document.querySelectorAll('.top__img-item');
        topImgItems.forEach(imgItem => imgItem.classList.remove('active'));
        const imgToShow = document.getElementById(`${id}-top__img`);
        if (imgToShow) {
          imgToShow.classList.add('active');
        }
      }
    };

    topItemElements.forEach(item => {
      item.addEventListener('click', handleTopItemClick);
    });

    return () => {
      // Clean up event listeners on component unmount
      topItemElements.forEach(item => {
        item.removeEventListener('click', handleTopItemClick);
      });
    };
